class UserController {
  static getEmail() {
    return window.document.cookie.email;
  }

  static signedIn() {
    return !!window.document.cookie.email;
  }

  static signIn(email, pass) {
    if(!window.document.cookie)
      window.document.cookie = {};

    window.document.cookie.email = email;
  }

  static signOut() {
    window.document.cookie = '';
  }

  static appendCartItem(catId, prodId, qty) {
    if(!window.document.cookie)
      window.document.cookie = {};

    if(!window.document.cookie.cart)
      window.document.cookie.cart = [];

    window.document.cookie.cart.push({
      catId: catId,
      prodId: prodId,
      qty: qty
    });
  }
}

export default UserController;
